.login {
    width: 375px;
    height: 100%;
    vertical-align: center;
    position: relative;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    position: absolute;
    top: 40%;
    right: 0;
    left: 0;
}

.cancel {
    margin-top: 44px;
    margin-left: 270px;
    border: none;
    background: none;
    border-radius: 50px;
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
}

.cross {
    position: relative;
    width: 40px;
    height: 40px;
}

.cross::before,
.cross::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: #FC440F;
    border-radius: 10px;
}

.cross::before {
    width: 48px;
    height: 4px;
    transform: translate(-50%, -50%);
}

.cross::after {
    width: 4px;
    height: 48px;
    transform: translate(-50%, -50%);
}

.input {
    width: 251px;
    height: 55px;
    padding: 11px 20px;
    font: normal normal normal 25px/33px Arial;
    background: #FCEFF9 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 20px #08070880;
    border: 2px solid #080708;
    border-radius: 30px;
}

.signIn {
    width: 169px;
    height: 55px;
    margin-top: 20px;
    background: #00CC99 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 20px #08070880;
    border-radius: 30px;
    opacity: 1;
    font: normal normal normal 25px/33px Arial;
    letter-spacing: 0px;
    color: #FFF7FC;
    border: none;
}
