* {
    box-sizing: border-box;
    margin: 0;
}

html,
body,
#root,
.pages,
.app {
    height: 100%;
    width: 100%;
}

.pages {
    display: flex;
    justify-content: center;
    align-items: center;
}

.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    vertical-align: center;
}

button {
    outline: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    tap-highlight-color: rgba(0, 0, 0, 0);
    touch-action: manipulation;
}