@font-face {
    font-family: 'SF Pro Regular';
    src: url('../../fonts/SF-Pro-regular.otf') format('truetype');
}

@font-face {
    font-family: 'SF Pro Thin';
    src: url('../../fonts/SF-Pro-thin.otf') format('truetype');
}

.content {
    padding-top: 123px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: transparent linear-gradient(180deg, #6600FF 0%, #00CC99 100%) 0% 0% no-repeat padding-box;
    gap: 40px;
}

.header {
    position: fixed;
    width: 100%;
    height: 80px;
    top: 0;
    background: #FCEFF9 0% 0% no-repeat padding-box;
    border-radius: 0px 0px 50px 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    z-index: 1;
}

.nav {
    display: flex;
    flex-direction: row;
    position: relative;
    align-items: center;
    width: 100%;
}

.logo {
    text-align: center;
    font: normal normal 900 35px/43px Arial;
    letter-spacing: 0px;
    color: #080708;
    margin-right: 20px;
    position: absolute;
    left: 34px;
}

.signIn {
    width: 98px;
    height: 36px;
    background: #FCEFF9 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 10px #6600FF66;
    border: 2px solid #6600FF;
    border-radius: 50px;
    font: bold normal normal 19px/25px Arial;
    letter-spacing: 0px;
    color: #6600FF;
    position: absolute;
    right: 36px;
}

.item {
    width: 308px;
    height: 368px;
    background: transparent linear-gradient(140deg, #FFF7FC66 0%, #FFFFFF66 0%, #FFFFFF00 100%) 0% 0% no-repeat padding-box;
    box-shadow: inset 3px 3px 6px #EEE5E9;
    border-radius: 30px;
    opacity: 1;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 0;
}

.footer {
    position: fixed;
    bottom: -6px;
    width: 100%;
    height: 153px;
    background: #FCEFF9 0% 0% no-repeat padding-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    border-radius: 50px 50px 0 0;
    padding-bottom: 10px;
}

.info {
    width: 278px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
}

.support {
    text-align: left;
    font: normal normal normal 20px/24px SF Pro Regular;
    letter-spacing: 0;
    color: #080708;
    white-space: nowrap;
    width: 278px;
}

.rights {
    width: 100%;
    text-align: left;
    font: normal normal 100 20px/24px SF Pro Thin;
    letter-spacing: 0;
    color: #080708;
}